body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* min-height: 100%; */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.footer-container{
  position:absolute;
  left:0;
  bottom:0;
  right:0;
  text-align: center;  
  color: white;
}
.content-heading{
  position: fixed;
  width: calc(100%);
  z-index: 99;
}
.content-wrapper .content-heading
{
  line-height: 0 !important;
}
.card-default{
  margin-top: 30px;
  overflow-y: visible;
  z-index: 1;
}
footer {
  /* clear: both; */
  position: fixed;
  left: 0;
  bottom: 0;
  /* height: 200px; */
  /* margin-top: -200px; */
}
header{
  position: fixed;
